<template>
    <div class="layout-container">
        <h1>{{ msg }}</h1>
        <router-link to="/">myself</router-link>
        <router-link to="/look">look</router-link>
        <router-link to="/message">message</router-link>
        <router-view></router-view>
    </div>
</template>

<script>
export default {
  name: 'Layout',
  data () {
    return {
      msg: '布局页'
    }
  }
}
</script>

<style lang="scss" scoped>
$color:red;
h1, h2 {
    font-weight: normal;
    color: $color;
}
</style>



